import React from 'react';
import Device from './Device.js';
import './App.css';
import {linkStations} from './stations.js';
import {devices} from './devices.js';

/*
*
*
*/
class App extends React.Component {
  constructor(props) {
    super(props);
    let parsedStations = [],
        parsedDevices = [];

    for (let s of linkStations) {
      parsedStations.push(JSON.parse(JSON.stringify(s)));
    }

    for (let d of devices) {
      parsedDevices.push(JSON.parse(JSON.stringify(d)));
    }

    this.state = {
      "stations": parsedStations,
      "devices": parsedDevices
    }
  }

  render() {

    let aDevices = [];
    for (let d of this.state.devices) {
      aDevices.push(<Device key={d.x + d.y} x={d.x} y={d.y} stations={this.state.stations} />);
    }

    return (
      <div className="App">
        {aDevices}
      </div>
    );
  }
}

export default App;
